@charset "UTF-8";
:root {
  --violet-hocarre: rgb(76, 2, 62);
  --beige: rgb(237, 213, 183);
  --light-blue: rgb(93, 202, 209);
  --electric-blue: rgb(69, 104, 255);
  --white: rgb(250, 250, 250);
  --light-gray: rgb(179, 179, 179);
  --facebook: #4764a3;
  --instagram: #CF26A2;
  --linkedin: #2867B2;
  --twitter: #1DA1F0 ;
  --phone: #29b310;
  --mail: rgb(255, 44, 44);
}

:root {
  --base-font: "Roboto", sans-serif;
}

/*---------------------------------------------------
* RESETS AND BOILEPLATE STYLE
-----------------------------------------------------*/
* {
  box-sizing: border-box;
  font-size: 100%;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  padding: 0px;
  margin: 0px;
  font-family: var(--base-font);
  background-color: #f7f7f7;
  line-height: 1.1;
  height: 100vh;
  scroll-behavior: smooth;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.flex {
  display: flex;
}

.flex-center {
  justify-content: center;
  align-items: center;
}

.flex-column {
  flex-direction: column;
}

.flex-wrap {
  flex-wrap: wrap;
}

.flex-wrap-reverse {
  flex-wrap: wrap-reverse;
}

/*
** RIPPLE EFFECT 
*/
span.ripple {
  position: absolute;
  transform: scale(0);
  animation: ripple 600ms linear;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.7);
}

@keyframes ripple {
  to {
    transform: scale(4);
    opacity: 0;
  }
}
/* ************ FLOATING ANIMATION ********* */
.floating {
  animation-name: floating;
  animation-duration: 1.5s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-out;
  margin-left: 30px;
  margin-top: 5px;
}

@keyframes floating {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, 20px);
  }
  100% {
    transform: translate(0, 0px);
  }
}
/* ************ TYPEWRITER ANIMATION ********* */
@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 85%;
  }
}
@keyframes blink-caret {
  from, to {
    border-color: transparent;
  }
  50% {
    border-color: var(--violet-hocarre);
  }
}
/*Management of the main modal window*/
.modal-container {
  /* display: none; */
  visibility: hidden;
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  transition: visibility 0.4s;
  z-index: 1000000000;
}

.modal-container.active {
  /* display: block; */
  visibility: visible;
}

.overlay {
  opacity: 0;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #333333d3;
  transition: opacity 0.4s 0.2s ease-out;
}

.modal-container.active .overlay {
  opacity: 1;
  transition: opacity 0.4s ease-out;
}

.modal {
  opacity: 0;
  width: 95%;
  max-width: 500px;
  min-width: 300px;
  min-height: 200px;
  padding: 30px;
  background: #fff;
  border-radius: 5px;
  position: absolute;
  top: 60%;
  left: 50%;
  transform: translate(-50%, calc(-50% - 50px));
  transition: opacity 0.4s ease-out, transform 0.4s ease-out;
}

.modal-container.active .modal {
  opacity: 1;
  transform: translate(-50%, -50%);
  transition: opacity 0.4s 0.2s ease-out, transform 0.4s 0.2s ease-out;
}

.close-modal {
  padding: 8px 10px;
  border: none;
  border-radius: 5px;
  font-size: 18px;
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
  background: #ff365e;
  color: #fff;
}

.modal h1 {
  margin-bottom: 20px;
  font-family: Montserrat, sans-serif;
  font-weight: 600;
  text-align: center;
  font-size: 20px;
}

.modal p {
  line-height: 1.4;
  margin-bottom: 15px;
  font-size: 17px;
}

.modal-content {
  display: flex;
  justify-content: space-around;
  margin-top: 20px;
}
.modal-content .modal-content-btn {
  padding: 15px;
  border-radius: 10px;
  color: white;
  background-color: var(--violet-hocarre);
  font-weight: bold;
  text-decoration: none;
}

.register-link {
  width: 100%;
  height: 100%;
  text-decoration: none;
  color: white;
}

/*-----------------------------
*GLOBAL NAVBAR STARTS
 ------------------------------ */
.header {
  width: 100%;
  min-height: 70px;
}

.navbar {
  width: 100%;
  padding: 0;
  max-height: 70px;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9999;
  background-color: #f7f1dc;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar__logo {
  margin-left: 2%;
}

.logo {
  width: 13.75rem;
  height: 100%;
}

.menu__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.menu__item {
  margin: 0 1.0625rem;
}

.item__link {
  font-size: 0.9375rem;
  color: black;
  padding-bottom: 10px;
  position: relative;
  text-transform: uppercase;
}

/* ====> hover effect -> underlined links */
.item__link.underlined:hover {
  color: var(--violet-hocarre);
}

.item__link.underlined::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  display: block;
  width: 100%;
  height: 0.125rem;
  background-color: var(--violet-hocarre);
  transform: scaleX(0);
  transition: transform 0.4s ease;
}

.item__link.underlined:hover::before {
  transform: scaleX(1);
}

/* ====> signUp CTA button*/
button {
  outline: 0;
  position: relative;
  overflow: hidden;
  transition: background 0.3s;
}

.sign-up-btn {
  border: 1.8px solid var(--violet-hocarre);
  color: var(--violet-hocarre);
  font-size: 1.125rem;
  background-color: transparent;
  border-radius: 0.625rem;
  padding: 0.3125rem 0.9375rem;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.3);
  font-weight: 700;
  cursor: pointer;
}

.sign-up-btn:hover {
  transition: 0.3s ease-out;
  background-color: var(--violet-hocarre);
  color: var(--white);
}

/* ------------------------------------
*GLOBAL NAVBAR ENDS
--------------------------------  */
/*--------------------------------
*HAMBURGER MENU STARTS
--------------------------------- */
.menu__hamburger {
  border: none;
  background-color: var(--white);
}

.menu__hamburger {
  display: none;
}

.menu__bar {
  display: block;
  width: 1.5625rem;
  height: 0.1875rem;
  margin: 0.3125rem auto;
  transition: all 0.3s ease-in-out;
  background-color: var(--violet-hocarre);
}

/*----------------------------------
*HAMBURGER MENU ENDS
-----------------------------------  */
/*----------------------------------
*MOBILE VERSION STARTS
-----------------------------------  */
@media only screen and (max-width: 57.6875rem) {
  .navbar {
    justify-content: space-between;
  }

  .menu__list {
    position: fixed;
    left: -1000%;
    top: 2.75rem;
    flex-direction: column;
    width: 100%;
    border-radius: 0.625rem;
    text-align: center;
    transition: 0.7s;
    background-color: #F2F0EA;
  }

  .menu__list.menu--is-active {
    left: 0;
    padding: 10px 0;
  }

  .menu__item {
    margin: 1.5625rem 0;
  }

  .menu__hamburger {
    display: block;
    cursor: pointer;
    margin-right: 1.875rem;
    justify-content: flex-end;
  }

  .menu__hamburger.menu--is-active .menu__bar:nth-child(2) {
    opacity: 0;
  }

  .menu__hamburger.menu--is-active .menu__bar:nth-child(1) {
    transform: translateY(8px) rotate(45deg);
  }

  .menu__hamburger.menu--is-active .menu__bar:nth-child(3) {
    transform: translateY(-8px) rotate(-45deg);
  }
}
/*----------------------------------
*HAMBURGER VERSION ENDS
-----------------------------------  */
.section-presentation {
