  width: 100%;
  background-color: white;
  color: #000000;
}

.presentation-container {
  width: 100%;
  height: 100%;
  display: flex;
}

/*------------------------------------------------------------------------
* LEFT PART STARTS
--------------------------------------------------------------------------*/
.presentation__left {
  width: 50%;
  height: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.presentation__header,
.presentation__body,
.presentation-cta {
  width: 90%;
}

.presentation__header {
  font-size: calc(1.425rem + 1.1vw);
  line-height: 1.3;
  font-weight: bold;
}

.gradient-text {
  background-image: linear-gradient(55deg, #3a0430, rgba(170, 0, 139, 0.767));
  background-size: 100%;
  background-repeat: repeat;
  -webkit-background-clip: text;
  -moz-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  -moz-text-fil-color: transparent;
}

.presentation-title,
.presentation-text {
  margin: 0.9375rem 0 0.9375rem 1.875rem;
}

.presentation__body {
  font-size: calc(1.2rem + 0.3vw);
  margin-bottom: 1.25rem;
  line-height: 1.5;
}

.presentation-cta__button {
  border: none;
  background-color: var(--violet-hocarre);
  color: #fff;
  border-radius: 1.5625rem;
  padding: 0.75rem 0.9375rem;
  font-weight: 600;
  cursor: pointer;
  font-size: calc(0.975rem + 0.175vw);
  position: relative;
  left: 5%;
  box-shadow: 0 0 0.5rem rgba(0, 0, 0, 0.25);
  transition: 0.25s;
}

.presentation-cta__button:hover {
  box-shadow: 0 0.5em 0.5em -0.4em var(--violet-hocarre);
  transform: translateY(-0.25em);
}

/*------------------------------------------------------------------------
* LEFT PART ENDS
--------------------------------------------------------------------------*/
/*------------------------------------------------------------------------
* RIGHT PART STARTS
--------------------------------------------------------------------------*/
.presentation__right {
  display: flex;
  place-items: center;
  width: 50%;
}
.presentation__right img {
  max-width: 90%;
}

/*------------------------------------------------------------------------
* RIGHT PART ENDS
--------------------------------------------------------------------------*/
@media screen and (max-width: 56.5625rem) {
  .section-presentation {
    height: 100vh;
    padding-bottom: 30px;
  }

  .presentation-container {
    flex-direction: column;
  }

  .presentation__left {
    height: 60%;
