  }

  .presentation-title,
.presentation-text {
    margin-left: 5px;
    margin-right: 5px;
  }

  .presentation__right {
    height: 40%;
    width: 100%;
    justify-content: center;
  }

  .presentation__right img {
    height: 100%;
    width: 90%;
  }

  .presentation__header {
    width: 70%;
  }

  .presentation__body {
    font-size: 1.125rem;
