  }
}
@media screen and (max-width: 820px) {
  .section-presentation {
    height: 825px;
  }
  .section-presentation .presentation__left {
    text-align: center;
  }

  .presentation-cta__button {
    left: 0%;
  }
}
@media screen and (max-width: 56.5625rem) {
