.presentation__body {
    width: 100%;
  }
}
/* ----- Just a little line up effect ----------- */
.line-up {
  animation: 2s animate-lineUp ease-out;
}

@keyframes animate-lineUp {
  0% {
    opacity: 0;
    transform: translateY(100%);
  }
  20% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
  80% {
    opacity: 1;
    transform: translateY(0%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}
