  background-color: rgba(0, 0, 0, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100000000;
  display: none;
  align-items: flex-end;
}
.cookie-consent-modal.active {
  display: flex;
}

.cookie-modal-content {
  background-color: #fff;
  padding: 1.5vw 3vw;
  border-radius: 10px 10px 0 0;
}
.cookie-modal-content .modal-top {
  display: flex;
  width: 100%;
}
.cookie-modal-content .modal-top .cookie-img {
  width: 27px;
  height: 27px;
  align-self: center;
  margin-right: 10px;
  background-image: url("/assets/images/cookies/cookie-consent.png");
  background-size: cover;
  background-repeat: no-repeat;
}
.cookie-modal-content .modal-title {
  font-size: calc(17px + 0.7vw);
}
.cookie-modal-content .modal-content-cookie span {
