.footer {
  width: 100%;
  min-height: 240px;
  height: 200px;
  background-color: #fff;
  text-decoration: none;
}

.footer-container {
  background-color: #fff;
  max-width: 75rem;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  font-size: 0.9375rem;
  padding: 1.875rem 0 0;
}

.footer-block {
  flex-basis: 20%;
  margin: 0;
}

.footer-block.block-logo {
  align-self: center;
  margin-right: 30px;
}

.footer-logo {
  width: 12.5rem;
  height: 9.375rem;
}

.block-list {
  padding-left: 0;
}

.block-title {
  font-size: 1.125rem;
  color: var(--violet-hocarre);
}

.block-title::after {
  content: "";
  display: block;
  margin-top: 0.3125rem;
  width: 5rem;
  height: 0.125rem;
  background-color: var(--violet-hocarre);
}

.block-title.company::after {
  content: "";
  display: block;
  margin-top: 0.3125rem;
  width: 3.125rem;
  height: 0.125rem;
  background-color: var(--violet-hocarre);
}

.block-list__item {
  padding: 0.375rem 0;
  transition: 0.3s;
}
.block-list__item:hover {
  text-decoration: underline;
}

.block-list.media {
  display: flex;
}

.block-list.media .block-list__item {
  padding-right: 0.9375rem;
  font-size: 1.375rem;
  transition: all 0.3s ease;
}

.block-list.media .block-list__item > a > i {
  color: #000;
}

.block-list.media .block-list__item > a > i:hover {
  color: var(--violet-hocarre);
  transform: scale(1.22);
}

.block-list__item > a {
  color: rgba(51, 51, 51, 0.911);
}

/* ===> RESPONSIVE*/
@media screen and (max-width: 62.5rem) {
  .footer-container {
    max-width: 100%;
    flex-wrap: wrap;
  }

  .footer-block {
    flex-basis: auto;
    margin: 1.25rem 1.25rem;
  }
}
@media screen and (max-width: 37.5rem) {
  .footer-container {
    justify-content: center;
    align-items: center;
    padding-top: 1.875rem;
  }

  .footer-block {
    flex-basis: 70%;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}
